






















import { Component, Prop, Vue } from 'vue-property-decorator';

@Component
export default class IntegerInc extends Vue {
  @Prop() value!: number;

  @Prop() index!: number;

  increment() {
    this.$emit('value-change', this.value + 1, this.index);
  }

  decrement() {
    this.$emit('value-change', this.value - 1, this.index);
  }
}
